/*

Aero Entities - Babylon side

*/

import { TransformNode } from "babylonjs/Meshes/transformNode";
import { Matrix, Quaternion, Vector3 } from "babylonjs/Maths/math.vector";
import { AeroAsset } from "./aeroAsset";

export class AeroEntity {
    public children: Array<AeroEntity> = [];
    public m_canDraw: boolean = true;
    public m_parent: AeroEntity;
    private m_quaternion: Quaternion = new Quaternion(0, 0, 0, 0);
    private m_rotation: Vector3 = new Vector3(0, 0, 0);
    private m_scale: Vector3 = new Vector3(1, 1, 1);
    private m_translation: Vector3 = new Vector3(0, 0, 0);
    private m_use_quats: boolean = false;
    private m_localName: string = '';
    private m_asset: AeroAsset;
    public babylon: TransformNode;

    constructor() {
        // this.m_rotation.impl = [0, 0, 0];
        // this.m_scale.impl = [1, 1, 1];
        // this.m_translation.impl = [0, 0, 0];
        this.m_asset = new AeroAsset(this);
    }

    public asset() {
        return this.m_asset;
    }

    public hasParent() {
        if (this.m_parent == undefined) {
            return false;
        }
        return true;
    }

    public globalName(): string {
        if (this.m_parent) {
            var parent: AeroEntity = this.m_parent;

            return parent.globalName() + '/' + this.m_localName;
        } else {
            return this.m_localName;
        }
    }

    public localMatrix() {
        //const matrix = new Matrix4();
        const matrixT = Matrix.Translation(this.translation().x, this.translation().y, this.translation().z);
        const matrixRx = Matrix.RotationX(this.rotation().x);
        const matrixRy = Matrix.RotationY(this.rotation().y);
        const matrixRz = Matrix.RotationZ(this.rotation().z);
        const matrixS = Matrix.Scaling(this.scale().x, this.scale().y, this.scale().z);
        return  matrixT.multiply(matrixRx).multiply(matrixRy).multiply(matrixRz).multiply(matrixS);
    }
    public localName() {
        return this.m_localName;
    }
    public parent() {
        return this.m_parent;
    }
    public quaternion() {
        return this.m_quaternion;
    }
    public rotation() {
        return this.m_rotation;
    }
    public scale() {
        return this.m_scale;
    }
    public setLocalName(localName: string) {
        this.m_localName = localName;
    }
    public canDraw() {
        return this.m_canDraw;
    }
    public setParent(parent: AeroEntity) {
        if (parent) {
            this.m_parent = parent;
            parent.children.push(this);
        }
    }
    public setQuaternion(quaternion: Array<number>) {
        this.m_quaternion.x = quaternion[0];
        this.m_quaternion.y = quaternion[1];
        this.m_quaternion.z = quaternion[2];
        this.m_quaternion.w = quaternion[3];
        this.m_rotation = new Vector3(0, 0, 0);
        this.m_use_quats = true;
    }
    public setRotation(rotation: Array<number>) {
        this.m_rotation.x = rotation[0];
        this.m_rotation.y = rotation[1];
        this.m_rotation.z = rotation[2];
        this.m_quaternion = new Quaternion(0, 0, 0, 0);
        this.m_use_quats = false;
    }
    public setScale(scale: Array<number>) {
        this.m_scale.x = scale[0];
        this.m_scale.y = scale[1];
        this.m_scale.z = scale[2];
    }
    public setTranslation(translation: Array<number>) {
        this.m_translation.x = translation[0];
        this.m_translation.y = translation[1];
        this.m_translation.z = translation[2];
    }
    public setCanDraw(canDraw: any) {
        this.m_canDraw = canDraw;
    }
    public translation(): Vector3 {
        return this.m_translation;
    }
    public useQuaternion() {
        return this.m_use_quats;
    }
    public visitChildren(func: Function, object: any) {
        for (const childI in this.children) {
            func(this.children[childI], object);
        }
    }
  }
