import { WebXRImageTracking } from 'babylonjs/XR/features/WebXRImageTracking';
import { WebXRPlaneDetector } from 'babylonjs/XR/features/WebXRPlaneDetector';
import { WebXRHitTest } from 'babylonjs/XR/features/WebXRHitTest';
import { WebXRState } from 'babylonjs/XR/webXRTypes';
import { WebXRSessionManager } from "babylonjs/XR/webXRSessionManager";
import { WebXRDefaultExperience } from "babylonjs/XR/webXRDefaultExperience";
import { FreeCamera } from "babylonjs/Cameras/freeCamera";
import { AbstractMesh } from "babylonjs/Meshes/abstractMesh";
import { Nullable } from "babylonjs/types";
import { Scene } from "babylonjs/scene";
import { AeroScene } from './aeroScene';
import { AeroAnchorNode } from "./aeroAnchorNode";
import { AeroEnvironment } from './aeroEnvironment';
import { IsWindowObjectExist } from "babylonjs/Misc/domManagement";
import { Tools } from "babylonjs/Misc/tools";
import { Matrix, Vector3, Quaternion } from "babylonjs/Maths/math.vector";

declare function importScripts(...urls: string[]): void;
declare var _native: any;

export class AeroWebXRHelper {

    constructor(scene_i: Scene, aeroScene_i: AeroScene) {
        this.babylonScene = scene_i;
        this.aeroScene = aeroScene_i;
        this.environment = new AeroEnvironment(scene_i);
    }

    private babylonScene: Scene;
    private aeroScene: AeroScene;
    public xrHelper: WebXRDefaultExperience;
    public sessionManager: WebXRSessionManager;

    private hitTestSystem: Nullable<WebXRHitTest>;
    private planeDetector: Nullable<WebXRPlaneDetector>;
    private imageTracking: Nullable<WebXRImageTracking>;

    private mode: string = "3D";
    private trackingMode: string = "plane";
    private groundOrientation: number = 1;

    private imageMesh: string = "";
    private checkedImageTracking: boolean = false;
    private imageTrackingAvailable: boolean = false;

    private anchorSet: boolean = false;
    //private inWebXR:boolean = false;
    private webXRAvailable: boolean = true;

    private mustAutoScale: boolean = false;
    private isAutoScaled: boolean = false;
    private mustResetScale: boolean = false;
    private lastPlacementScale: number = 1;

    /*
        Environment
    */
    private environment: AeroEnvironment;
    public GetEnvironment() {
        return this.environment;
    }

    private MoveAnchor(center: Vector3, normal: Vector3, rotationAboutNormal: number, scale: number) {

        var aeroScene: AeroScene = this.aeroScene;

        var aeroAnchor: Nullable<AeroAnchorNode> = this.placementNode;
        if (!aeroAnchor) {
            aeroAnchor = aeroScene.GetAnchor();
        }

        aeroAnchor.scaling = new Vector3(scale, scale, scale);

        aeroAnchor.position = center;

        var eulers;

        if (this.groundOrientation == 1) {
            //Calculate X/Z rotation matrix using up vector and surface normal
            var zeroYRotationVector = new Vector3(0, 1, 0);
            var normalRotation = new Quaternion();
            Quaternion.FromUnitVectorsToRef(zeroYRotationVector, normal, normalRotation);
            var normalTransformMatrix = new Matrix();
            normalRotation.toRotationMatrix(normalTransformMatrix);

            var matrix = normalTransformMatrix;
            if (rotationAboutNormal != 0) {
                //Calculate spin matrix around normal
                var spinQuaternion = Quaternion.RotationAxis(normal, rotationAboutNormal);
                var spinTransformMatrix = new Matrix();
                spinQuaternion.toRotationMatrix(spinTransformMatrix);

                //Multiply to get combined matrix
                matrix = normalTransformMatrix.multiply(spinTransformMatrix);
            }

            var quat = Quaternion.FromRotationMatrix(matrix);
            eulers = quat.toEulerAngles();
        } else {
            //Calculate X/Y rotation matrix using out vector and surface normal
            var zeroZRotationVector = new Vector3(0, 0, 1);
            var normalRotation = new Quaternion();
            Quaternion.FromUnitVectorsToRef(zeroZRotationVector, normal, normalRotation);
            var normalTransformMatrix = new Matrix();
            normalRotation.toRotationMatrix(normalTransformMatrix);

            var matrix = normalTransformMatrix;
            var quat = Quaternion.FromRotationMatrix(matrix);
            eulers = quat.toEulerAngles();
        }

        if (this.groundOrientation == 2) {
            eulers.z = 0;
            aeroAnchor.rotation = eulers;

        } else {
            eulers.y -= Math.PI;
            aeroAnchor.rotation = eulers;
        }

        var groundMesh = this.babylonScene.getMeshByName("Ground");
        if (groundMesh) {
            groundMesh.position = center;
            groundMesh.rotation = eulers;
            groundMesh.scaling = new Vector3(scale, scale, scale);
        }
    }

    private OnTrackingUpdate(mesh: AbstractMesh, vertexData: any) {
        var name: string = mesh.name;
        if (this.trackingMode == "image") {
            this.imageMesh = name;
        }

        const meshBounds: any = mesh.getHierarchyBoundingVectors();

        var center: Vector3 = new Vector3(0, 0, 0);
        center.x = (meshBounds.max.x + meshBounds.min.x) / 2;
        center.y = (meshBounds.max.y + meshBounds.min.y) / 2;
        center.z = (meshBounds.max.z + meshBounds.min.z) / 2;

        //Calculate surface normal
        var x2: number, y2: number, z2: number;
        var normal: Vector3;
        // if(meshIsGlobal) {
        //     //From Aero, positions are already global
        //     x2 = (vertexData.positions[9] + vertexData.positions[6])/2;
        //     y2 = (vertexData.positions[10] + vertexData.positions[7])/2;
        //     z2 = (vertexData.positions[11] + vertexData.positions[8])/2;
        //     normal = new Vector3(vertexData.normals[0],vertexData.normals[1],vertexData.normals[2]);

        //     //console.log("OnTrackingUpdate: " + normal.x + "," +  + normal.y + "," +  + normal.z);
        // } else {
            //From WebXR, positions are local and need to be transformed into world coords
            var p1: Vector3 = new Vector3(vertexData.positions[6], vertexData.positions[7], vertexData.positions[8]);
            var p2: Vector3 = new Vector3(vertexData.positions[9], vertexData.positions[10], vertexData.positions[11]);

            var p1W: Vector3 = Vector3.TransformCoordinates(p1, mesh.getWorldMatrix());
            var p2W: Vector3 = Vector3.TransformCoordinates(p2, mesh.getWorldMatrix());

            x2 = (p1W.x + p2W.x) / 2;
            y2 = (p1W.y + p2W.y) / 2;
            z2 = (p1W.z + p2W.z) / 2;

            var n1: Vector3 = new Vector3(vertexData.normals[0], vertexData.normals[1], vertexData.normals[2]);
            normal = Vector3.TransformNormal(n1, mesh.getWorldMatrix());
        //}
        normal.normalize();

        //Calculate Y rotation using Image Z vector and Identity Z Vector
        var zeroRotationZVector: Vector3 = new Vector3(0, 0, 1);
        var planeCenterZVector: Vector3 = new Vector3(-(x2 - center.x), (y2 - center.y), (z2 - center.z));
        planeCenterZVector.normalize();
        var q: Quaternion = new Quaternion();
        Quaternion.FromUnitVectorsToRef(zeroRotationZVector, planeCenterZVector, q);
        var eulersFromCenterZVector: Vector3 = q.toEulerAngles();

        var angleHeading: number  = -eulersFromCenterZVector.y - Math.PI / 2;

        var scale: number  = 1.0;

        this.MoveAnchor(center, normal, angleHeading, scale);
    }

    /*

        Camera

    */

    public SetCamera(px: number, py: number, pz: number, ox: number, oy: number, oz: number, minZ: number, maxZ: number, hFov: number) {

        if (!this.babylonScene) {
            return;
        }

        var camera: FreeCamera = this.babylonScene.activeCamera as FreeCamera;
        if (!camera) {
            return;
        }
        camera.position.x = px;
        camera.position.y = py;
        camera.position.z = pz;

        camera.rotation.x = ox;
        camera.rotation.y = oy;
        camera.rotation.z = oz;

        camera.fov = hFov;

        camera.minZ = minZ;
        camera.maxZ = maxZ;
    }

    /*

    Modes

    */
    private enterARCallback: Function;
    private exitARCallback: Function;

    public SetEnterARCallback(callback: Function) {
        this.enterARCallback = callback;
    }

    public SetExitARCallback(callback: Function) {
        this.exitARCallback = callback;
    }

    private ManageModes() {
        this.xrHelper.baseExperience.onStateChangedObservable.add((state: any) => {
            switch (state) {
                case WebXRState.IN_XR:
                    break;
                case WebXRState.ENTERING_XR:
                    this.EnterARMode(true);
                    break;
                case WebXRState.EXITING_XR:
                    break;
                case WebXRState.NOT_IN_XR:
                    this.ExitARMode(true);
                    break;
            }
        });
    }

    public EnterARMode(fromWebXR: boolean) {
        this.mode = "AR";
        this.mustAutoScale = true;
        this.isAutoScaled = false;
        this.mustResetScale = false;

        //TODO: Grab current camera - store away for later
        //Create a FreeCamera

        this.anchorSet = false;

        var anchor: AeroAnchorNode = this.GetAnchor();
        if (!this.aeroScene.SceneHasImageMarkers()) {
            this.StopImageTracking();
            this.trackingMode = "plane";
            this.groundOrientation = anchor.desiredAlignment;
            //this.SendAeroCommand("startAR:Planar");
        } else {
            this.StopPlaneTracking();
            this.StopHitTesting();
            this.HideScene();
            this.trackingMode = "image";
            this.groundOrientation = anchor.desiredAlignment;
            var markerPath;
            markerPath = this.aeroScene.GetImageMarkerURL();
            // if(this.xrHelper == undefined) {
            //     var fullPath = new URL(markerPath, document.baseURI).href;
            //     this.SendAeroCommand("startAR:" + anchor.physicalWidth + "_" + fullPath);
            //     if(this.xrHelper == undefined)
            //         return;
            // } else {
                this.StartWebXRImageTracking(markerPath, anchor.physicalWidth);
            //}

        }

        var aeroScene: AeroScene = this.aeroScene;
        if (this.groundOrientation == 2) {
            aeroScene.SetShadowDirection(0, 0, 10);
        }
        else {
            aeroScene.SetShadowDirection(0, -10, 0);
        }

        this.environment.RemoveAllPlanes();

        if (this.enterARCallback) {
            this.enterARCallback();
        }
        this.aeroScene.ResetCameraAndAnchor();
    }
    public OnTap(): boolean {
        if (!this.anchorSet || this.placementNode != null)  {
            this.SetAnchor();
            return true;
        }

        return false;
    }

    private HideScene() {
        this.GetAnchor().setEnabled(false);
    }

    private ShowScene() {
        this.GetAnchor().setEnabled(true);
    }
    private GetAnchor() {
        if (this.placementNode != null) {
            return this.placementNode;
        }

        return this.aeroScene.GetAnchor();
    }
    private placementNode: Nullable<AeroAnchorNode> = null;
    public PlaceScene(anchorNode: AeroAnchorNode) {
        if (this.mode == "AR") {
            this.placementNode = anchorNode;
            this.InitializeHitTesting();
            this.InitializePlaneTracking();
        }
    }

    private SetAnchor() {

        //Shut down XR
        this.StopHitTesting();
        this.StopImageTracking();
        this.StopPlaneTracking();

        this.anchorSet = true;

        //Hide any planes that were visible
        this.environment.RemoveAllPlanes();
        this.ShowScene();

        //TODO: callback - anchor is placed

        //Run any onStart behaviors
        if (this.placementNode != null) {
            var scene: AeroScene = this.placementNode.aeroScene;
            this.placementNode = null;
            scene.Start();
        } else {
            //this.aeroScene.Start();
        }
    }

    public ExitARMode(fromWebXR: boolean) {
        var aeroScene: AeroScene = this.aeroScene;
        this.anchorSet = false;
        this.mode = "3D";
        this.environment.RemoveAllPlanes();

        //TODO: Grab saved camera and set back

        aeroScene.ResetCameraAndAnchor();
        aeroScene.SetShadowDirection(0, -10, 0);

        this.InitializeWebXR(function() {});
        if (this.exitARCallback) {
            this.exitARCallback();
        }
        //this.SendAeroCommand("stopAR");
    }

    public AutoScale() {
        if (this.isAutoScaled) {
            this.mustResetScale = true;
        } else {
            this.mustAutoScale = true;
        }
    }

/*

WebXR

*/

    public async IsWebXRAvailable()
    {
        // if (this.IsAeroMessageHandlerAvailable()) {
        //     console.log("Running on iOS with host app providing AR");
        //     this.webXRAvailable = true;
        //     return;
        // }

        this.webXRAvailable = true;
        let arAvailable = await WebXRSessionManager.IsSessionSupportedAsync('immersive-ar');
        if (!arAvailable) {
            this.webXRAvailable = false;
            return;
        }
    }

    public ARSupported() {
        // if (this.IsAeroMessageHandlerAvailable()) {
        //     console.log("AeroWebViewer running inside Aero iOS");
        //     return true;
        // }

        if (!this.webXRAvailable) {
            return false;
        }

        if (this.aeroScene.SceneHasImageMarkers() && !this.ImageTrackingAvailable()) {
            return false;
        }

        return true;
    }

    public InitializeWebXR(callback: Function) {

        // if (this.IsAeroMessageHandlerAvailable()) {
        //     console.log("Running on iOS with host app providing AR");
        //     this.webXRAvailable = true;
        //     return;
        // }

        if (!this.webXRAvailable) {
            return;
        }

        //WebXR
        try {
            this.babylonScene.createDefaultXRExperienceAsync({
                uiOptions: {
                    sessionMode: "immersive-ar",
                    referenceSpaceType: "unbounded",
                },
                disableDefaultUI: (typeof _native !== undefined), //Disable UI in Babylon Native...TODO: add UI back manually
                disableTeleportation: true,
                optionalFeatures: ["hit-test", "plane-detection", "hand-tracking"]
            }).then((xrHelper) => {
                if (!xrHelper || !xrHelper.baseExperience || !xrHelper.baseExperience.sessionManager) {
                    this.webXRAvailable = false;
                    callback(false);
                    return;
                }
                //WebXR default experience successfull initialized
                this.xrHelper = xrHelper;
                this.sessionManager = this.xrHelper.baseExperience.sessionManager as WebXRSessionManager;

                this.InitializePlaneTracking();
                this.InitializeHitTesting();
                this.ManageModes();
                callback(true);
            }, (error) => {
                // no xr...
                console.log('WebXR support is unavailable');
                this.webXRAvailable = false;
                callback(false);
            });
        } catch (e) {
            callback(false);
        }
    }

    /*
        Image Tracking
    */

    private ImageTrackingAvailable() {
        if (this.checkedImageTracking == true) {
            return this.imageTrackingAvailable;
        }

        if (!this.xrHelper) {
            return false;
        }

        var relativePath: string = "";
        var physicalWidth: number = 1.0;
        const fm = this.xrHelper.baseExperience.featuresManager;
        this.imageTracking = null;
        try {
            this.imageTracking = fm.enableFeature(WebXRImageTracking.Name, 'latest', {
                images: [
                    {
                        src: relativePath,    // insert image marker
                        estimatedRealWorldWidth: physicalWidth
                    },
                ]
            }) as WebXRImageTracking;
        } catch (e) {

        }
        this.checkedImageTracking = true;

        if (this.imageTracking) {
            this.imageTracking.dispose();
            this.imageTrackingAvailable = true;
            return true;
        }
        this.imageTrackingAvailable = false;
        return false;
    }

    private StartWebXRImageTracking(relativePath: string, physicalWidth: number) {

        if (this.xrHelper == undefined) {
            return;
        }

        const fm = this.xrHelper.baseExperience.featuresManager;

        this.imageTracking = fm.enableFeature(WebXRImageTracking.Name, 'latest', {
            images: [
                {
                    src: relativePath,
                    estimatedRealWorldWidth: physicalWidth
                },
            ]
        }) as WebXRImageTracking;

        if (this.imageTracking) {
            this.imageTracking.onUntrackableImageFoundObservable.add((idx: any) => {
                console.log('Aero cannot track this image', idx);
                this.HideScene();
            });

            this.imageTracking.onTrackableImageFoundObservable.add((image: any) => { //is this necessary
            });

            this.imageTracking.onTrackedImageUpdatedObservable.add((image: any) => {
                this.ShowScene();
                var plane: any = this.environment.UpdateXRImageMarker(image);
                //TODO - if hit test system is working, use that for consistency
                this.OnTrackingUpdate(plane.mesh, plane.vertexData);
            });
        }

    }
    private StopImageTracking() {
        //Shut down hit testing
        if (this.imageTracking) {
            this.imageTracking.dispose();
            this.imageTracking = null;
        }

        if (this.imageMesh != "") {
            var mesh: Nullable<AbstractMesh> = this.babylonScene.getMeshByName(this.imageMesh);
            if (mesh) {
                mesh.setEnabled(false);
                mesh.dispose();
            }
            this.imageMesh = "";
        }
    }

    /*

        Plane Tracking

    */

    private InitializePlaneTracking() {
        try {
            // featuresManager from the base webxr experience helper
            const fm = this.xrHelper.baseExperience.featuresManager;
            this.planeDetector = fm.enableFeature(WebXRPlaneDetector.Name, "latest") as WebXRPlaneDetector;

            //Plane tracking succeeded.  We need earcut to make polygons out of plane data
            var earcutURL: string = this.aeroScene.GetEarcutURL();
            if (IsWindowObjectExist()) {
                Tools.LoadScriptAsync(earcutURL);
            } else {
                importScripts(earcutURL);
            }

            //Set up the plane tracking obervables
            if (this.planeDetector) {
                this.planeDetector.onPlaneAddedObservable.add((plane: any) => {
                    this.environment.UpdateXRPlane(plane);
                });

                this.planeDetector.onPlaneRemovedObservable.add((plane: any) => {
                    var name: string = "Environment_" + plane.id;
                    this.environment.RemoveAPlane(name);
                });

                this.planeDetector.onPlaneUpdatedObservable.add((plane: any) => {
                    this.environment.UpdateXRPlane(plane);
                });
            }
        } catch (e) {
            console.log("Setup Plane Tracking Failure! ");
        }
    }
    private StopPlaneTracking() {
        //Shut down hit testing
        if (this.planeDetector) {
            this.planeDetector.dispose();
            this.planeDetector = null;
        }

    }

    /*

        Hit Testing

    */

    private InitializeHitTesting() {

        try {
            this.hitTestSystem = this.xrHelper.baseExperience.featuresManager.enableFeature(
                WebXRHitTest.Name,
                'latest',
                { disablePermanentHitTest: (typeof _native !== undefined) }
            ) as WebXRHitTest;
        } catch (e) {

        }

        if (this.hitTestSystem) {
            this.hitTestSystem.onHitTestResultObservable.add((results: any) => {
                if (results == undefined || results.length == 0) {
                    return;
                }
                var camera = this.xrHelper.baseExperience.camera;
                var normal = Vector3.TransformNormal(Vector3.Up(), results[0].transformationMatrix).normalize();
                var position = results[0].position;
                this.OnHitTestResult(position, normal, camera);
            });
        }
    }

    public OnHitTestResult(position: Vector3, normal: Vector3, camera: FreeCamera) {
        if ((this.anchorSet || camera == undefined || this.trackingMode != "plane") && this.placementNode == null) {
            return;
        }

        if (this.mode == "3D") {
            return;
        }

        if (position.x == 0 && position.y == 0 && position.z == 0) {
            return;
        }

        var aeroScene: AeroScene = this.aeroScene;

        var pickx: number = position.x;
        var picky: number = position.y;
        var pickz: number = position.z;
        var picknx: number = normal.x;
        var pickny: number = normal.y;
        var picknz: number = normal.z;

        var pickPoint: Vector3 = new Vector3(pickx, picky, pickz);
        var pickNormal: Vector3 = new Vector3(picknx, pickny, picknz);

        //Scale based on distance to the camera
        var scale: number = this.lastPlacementScale;
        if (this.mustAutoScale == true) {
            this.mustAutoScale = false;
            this.isAutoScaled = true;
            this.mustResetScale = false;
            scale = 0.5 * Vector3.Distance(camera.position, pickPoint) / aeroScene.sceneWidth;
            if (scale > 1.0) {
                scale = 1.0;
            }
        } else if (this.mustResetScale) {
            scale = 1.0;
            this.isAutoScaled = false;
            this.mustResetScale = false;
            this.mustAutoScale = false;
        }
        this.lastPlacementScale = scale;

        var angleHeading: number =  0;
        if (Math.abs(pickNormal.y) > Math.abs(pickNormal.x) && Math.abs(pickNormal.y) > Math.abs(pickNormal.z)) {
            var eulers: Vector3 = camera.rotation;
            if (eulers.x == 0 && eulers.y == 0 && eulers.z == 0) {
                eulers = camera.rotationQuaternion.toEulerAngles();
            }
            angleHeading = eulers.y;
        }

        this.MoveAnchor(pickPoint, pickNormal, angleHeading, scale);

    }
    private StopHitTesting() {
        //Shut down hit testing
        if (this.hitTestSystem) {
            this.hitTestSystem.paused = true;
            this.hitTestSystem.dispose();
            this.hitTestSystem = null;
        }
    }

/*

App clip commuincation
AERO.UpdateARPlane
AERO.RemovePlane
AERO.OnHitTest
AERO.UpdateCamera

SendAeroCommand
hideAllPlanes (May not need)
startAR
Plane SendAeroCommand("startAR:Planar");
Image: SendAeroCommand("startAR:" + physicalWidth + "_" + fullPath);

stopAR

*/

    private IsAeroMessageHandlerAvailable() {
        var isIOS: boolean = false;
        if (navigator.userAgent.includes("iPad") ||
            navigator.userAgent.includes("iPhone")) {
            //Supports XR, is windows and is edge - assume Hololens for now
            isIOS = true;
        }

        if (!isIOS) {
            console.log("IsAeroMessageHandlerAvailable - not inside iOS");
            return false;
        }
        if (!window.hasOwnProperty("webkit")) {
            console.log("IsAeroMessageHandlerAvailable - no webkit");
            return false;
        }

        var windowVariant: any = window;
        var webkit: any = windowVariant.webkit;

        if (!webkit.messageHandlers) {
            console.log("IsAeroMessageHandlerAvailable - no messageHandlers");
            return false;
        }
        if (!webkit.messageHandlers.aero) {
            console.log("IsAeroMessageHandlerAvailable - no aero object");
            return false;
        }

        console.log("IsAeroMessageHandlerAvailable - Aero message handler");

        return true;
    }

    public SendAeroCommand(command: string) {
        if (!this.IsAeroMessageHandlerAvailable()) {
            return;
        }

        var windowVariant: any = window;
        var webkit: any = windowVariant.webkit;
        webkit.messageHandlers.aero.postMessage(command);
    }

}
