/*

Aero Assets

*/

import { AeroEntity } from "./aeroEntity";
import { Vector3 } from "babylonjs/Maths/math.vector";

export class AeroBoundingBox {
    public min: Vector3 = new Vector3(0, 0, 0);
    public max: Vector3 = new Vector3(0, 0, 0);
}

export class AeroAsset {
    public entity: AeroEntity;
    public m_bounds: AeroBoundingBox;
    public m_maxDiameter: number = 1;
    public m_uri: string = "";

    constructor(entity_i: AeroEntity) {
        this.entity = entity_i;
    }

    public bounds(): AeroBoundingBox {
        if (this.m_bounds) {
            return this.m_bounds;
        } else {
            return {min: new Vector3(0, 0, 0), max: new Vector3(0, 0, 0)};
        }
    }

    public maxDiameter() {
        if (this.m_maxDiameter !== undefined) {
            return this.m_maxDiameter;
        } else {
            return 1;
        }
    }

    public uri() {
        return this.m_uri;
    }

    public setBounds(bounds: AeroBoundingBox) {
        this.m_bounds = bounds;
    }

    public setMaxDiameter(maxDiameter: number) {
        this.m_maxDiameter = maxDiameter;
    }
    public setUri(uri: string) {
        this.m_uri = uri;
    }
}
