/*

Anchor Node

*/

import { TransformNode } from "babylonjs/Meshes/transformNode";
import { Scene } from "babylonjs/scene";
import { AeroScene } from './aeroScene';

export class AeroAnchorNode extends TransformNode {
    constructor(name: string, scene: Scene, aeroScene: AeroScene) {
        super(name, scene);
        this.aeroScene = aeroScene;
    }

    //Horizontal or Vertical (1/2)
    public desiredAlignment: number = 1;

    //Filled in if anchor is an image anchor
    public imageMarkerPath: string = ""; //"imageMarker.filename"
    public physicalWidth: number = 0;
    public aeroScene: AeroScene;

}
