/*

AeroPublishHelper - code needed to use files published on Aero's back end

*/

import { WebRequest } from 'babylonjs/Misc/webRequest';

declare function importScripts(...urls: string[]): void;

declare global {
    interface Window {
        branch: any;
    }
}

export class AeroPublishHelper {

    constructor() {
     }

    public LoadAeroManifest(publicLink: string, cpURL: string, manifestParsedCallback: Function) {
        this.loadAeroManifest(publicLink, cpURL, manifestParsedCallback);
    }

    //private format: AeroFormat;
    private gAeroManifestURL: string = "";
    private gAeroCpURL: string = "";
    private gAeroComponentTemplatePath: string = "";
    private gAeroPublicLink: string = "";
    private gAeroScenePath: string = "";
    private gAeroComponentMap: Map<string, string> = new Map<string, string>();
    private gAeroJSURL: string = "";
    private gAeroManifestParsedCallback: Function;

    //Base function for loading the contents of a URL
    private loadTextFromURL(url: string, callback: Function) {
        // read text from URL location
        var request = new WebRequest();

        request.open('GET', url);
        request.send();
        request.addEventListener("readystatechange", () => {
            if (request.readyState === 4 && (request.status === 200 || request.status === 0)) {
                callback(request.responseText);
            }
        });
    }

    //Assumes gAeroManifestURL and gAeroComponentTemplatePath are set
    private parseManifest(mainfestText: string) {
        //Raw JSON, so these are type any
        var manifestJson: any = JSON.parse(mainfestText);
        var components: any = manifestJson.components;
        var children: any = manifestJson.children;

        //Find the scene component and construct it's URL
        for (const jsonComponent of components) {
            var componentPath: string = jsonComponent["path"];
            if (componentPath.includes("SCENE_JSON") || componentPath.includes(".aero")) {

                this.gAeroScenePath = this.gAeroComponentTemplatePath;
                this.gAeroScenePath = this.gAeroScenePath.replace("{path}", jsonComponent["id"]);

                var version: string = "0";
                if (jsonComponent.version != undefined) {
                    version = jsonComponent.version;
                }
                this.gAeroScenePath = this.gAeroScenePath.replace("{version}", version);
                break;
            }
        }

        //Asset components - build gAeroComponentMap and grab the AeroWebViewer.js on the way through
        for (const jsonChild of children) {
            var assetPath: string = jsonChild["path"];
            var grandChildren: any = jsonChild["children"];
            if (grandChildren != undefined) {
                for (const jsonGrandchild of grandChildren) {
                    var grandChildPath: string = assetPath;
                    grandChildPath += "/" + jsonGrandchild["path"];

                    var subComponents: any = jsonGrandchild["components"];
                    if (subComponents != undefined) {
                        for (const jsonComponent of subComponents) {
                            var subComponentPath: string = grandChildPath;
                            subComponentPath += "/" + jsonComponent["path"];

                            var componentURL: string = this.gAeroComponentTemplatePath;
                            componentURL = componentURL.replace("{path}", jsonComponent["id"]);

                            var version: string = "0";
                            if (jsonComponent.version != undefined) {
                                version = jsonComponent.version;
                            }
                            componentURL = componentURL.replace("{version}", version);

                            this.gAeroComponentMap.set(subComponentPath, componentURL);
                            if (subComponentPath.includes("AeroWebViewer.js")) {
                                this.gAeroJSURL = componentURL;
                            }
                        }
                    }
                }
            }
        }

        //Call back to say we're done
        this.gAeroManifestParsedCallback(this.gAeroScenePath, this.gAeroComponentMap);
    }

    public loadAeroManifest(publicLink: string, cpURL: string, manifestParsedCallback: Function) {
        this.gAeroCpURL = cpURL;
        this.gAeroPublicLink = publicLink;
        this.gAeroManifestParsedCallback = manifestParsedCallback;

        if (this.gAeroCpURL != undefined && !this.gAeroCpURL.includes("CP_URL") && this.gAeroCpURL != "") {
            //The cp URL has been provided by the back end
            this.loadTextFromURL(this.gAeroCpURL, (componentLinkResponseText: string) => {
                var componentLinkResponseJSON: any = JSON.parse(componentLinkResponseText);
                var links: any = componentLinkResponseJSON["_links"];
                this.gAeroManifestURL = links["dcx_manifest"]["href"];
                this.gAeroComponentTemplatePath = links["dcx_component"]["href"];
                this.loadTextFromURL(this.gAeroManifestURL, this.parseManifest);

            });
        } else if (this.gAeroPublicLink.includes("SCENE_JSON")) {
            this.gAeroScenePath = this.gAeroPublicLink;
            this.gAeroJSURL = this.gAeroPublicLink.substring(0, this.gAeroPublicLink.indexOf("SCENE_JSON"));
            this.gAeroJSURL += "Assets/AeroWebViewer/AeroWebViewer.js";
            this.gAeroManifestParsedCallback(this.gAeroJSURL, this.gAeroScenePath, this.gAeroComponentMap);
        } else if (this.gAeroPublicLink.includes(".aero")) {
            this.gAeroScenePath = this.gAeroPublicLink;
            this.gAeroJSURL = this.gAeroPublicLink.substring(0, this.gAeroPublicLink.lastIndexOf("/") + 1);
            this.gAeroJSURL += "Assets/AeroWebViewer/AeroWebViewer.js";
            this.gAeroManifestParsedCallback(this.gAeroJSURL, this.gAeroScenePath, this.gAeroComponentMap);
        } else if (this.gAeroPublicLink != "") {
            var branchKey: string = 'key_live_cdTvTtwBPh3UQwdZZNlwbbdhqDakuKSj';

            var url = "https://api2.branch.io/v1/url?url=";
            url += this.gAeroPublicLink;
            url += "&branch_key=" + branchKey;

            this.loadTextFromURL(url, (apiResponseText: string) => {
                var apiResponseJSON: any = JSON.parse(apiResponseText);
                //console.log(apiResponseText);
                var apiLink: string = apiResponseJSON.data["aero_pub_url"];
                this.loadTextFromURL(apiLink, (apiLinkResponseText: string) => {
                    //console.log(apiLinkResponseText);
                    var apiLinkResponseJSON: any = JSON.parse(apiLinkResponseText);
                    var componentLink: string = apiLinkResponseJSON["cp_url"];
                    componentLink += "?api_key=Aero_Content_Service1";
                    this.loadTextFromURL(componentLink, (componentLinkResponseText: string) => {
                        //console.log(componentLinkResponseText);
                        var componentLinkResponseJSON: any = JSON.parse(componentLinkResponseText);
                        var links: any = componentLinkResponseJSON["_links"];
                        this.gAeroManifestURL = links["dcx_manifest"]["href"];
                        this.gAeroComponentTemplatePath = links["dcx_component"]["href"];
                        this.loadTextFromURL(this.gAeroManifestURL, (manifest: string) => {
                            this.parseManifest(manifest);
                        });

                    });

                });
            });

        } else {
            console.log("Public link or manifest url + component path required");
        }
    }
}