import { Nullable } from "babylonjs/types";
import { AbstractMesh } from "babylonjs/Meshes/abstractMesh";
import { SceneLoader, ISceneLoaderPluginAsync, ISceneLoaderProgressEvent, ISceneLoaderPluginFactory, ISceneLoaderPlugin, ISceneLoaderAsyncResult } from "babylonjs/Loading/sceneLoader";
import { AssetContainer } from "babylonjs/assetContainer";
import { Scene } from "babylonjs/scene";

import { AeroLoadingOptions } from "./aeroLoadingOptions";
import { AeroPublishHelper } from "./format/aeroPublishHelper";
import { AeroScene } from './format/aeroScene';

//Will be set by Aero on the outside if we are inside Aero
declare var dcxInfo: any;

/**
 * Aero file type loader.
 * This is a babylon scene loader plugin.
 */
export class AeroFileLoader implements ISceneLoaderPluginAsync, ISceneLoaderPluginFactory {

    /**
     * Defines if we play behaviors on load.
     */
    public static AUTO_PLAY = true;

    /**
     * Defines the name of the plugin.
     */
    public name = "aero";
    /**
     * Defines the extension the plugin is able to load.
     */
    public extensions = ".aero";

    public _assetContainer: Nullable<AssetContainer> = null;

    public _loadingOptions: AeroLoadingOptions;

    //Publish
    private publish: AeroPublishHelper = new AeroPublishHelper();

    //Aero Scene
    private scene: AeroScene;
    private sceneLoadedPromise: Promise<Array<AbstractMesh>>;
    private savedPublishURL: string = "";
    public LoadAeroWebScene(url: string, cpURL: string, componentMap: any, onProgress: Nullable<(event: ISceneLoaderProgressEvent) => void>): Promise<Array<AbstractMesh>> {

        var options: any = new Object();
        options.componentMap = componentMap;

        if (!this.sceneLoadedPromise) {
            this.sceneLoadedPromise = new Promise<Array<AbstractMesh>>((resolve, reject) => {
                this.scene.SetSceneLoadedCallback((meshes: Array<AbstractMesh>) => {
                    resolve(meshes);
                });
            });
        }

        if ((cpURL != undefined && cpURL.includes("CP_URL")) ||
                url.includes("adobeaero.app.link")
            ) {
            this.savedPublishURL = url;
            this.publish.LoadAeroManifest(url, cpURL, (theURL: string, cmap: any) => {
                this.LoadAeroWebScene(theURL, cpURL, cmap, onProgress);
            });

        } else {
            //This scene has a web viewer - load in the JS, eval it and then run the viewer
            if (onProgress) {
                this.SetProgressCallback((isValid: boolean, done: number, total: number) => {
                    onProgress({
                        lengthComputable: isValid,
                        loaded: done,
                        total: isValid ? total : 0
                    });
                });
            }

            this.Initialize();
            this.scene.LoadScene(url, componentMap, options);
            this.scene.SetPublishURL(this.savedPublishURL);

            return this.sceneLoadedPromise.then(() => {
               return this.scene.GetLoadedMeshes();
           });
        }

         //Return an array with all Mesh
         return this.sceneLoadedPromise.then(() => {
            return this.scene.GetLoadedMeshes();
        });
    }

    private Initialize() {
        //Set up and scene loading callback for preprocessing
        this.scene.InitializeSceneLoader();
    }
    public Start() {
        this.scene.Start();
    }

    public SetProgressCallback(callback: Function) {
        this.scene.progressCallback = callback;
    }

    public _progressCallback: Nullable<(event: ISceneLoaderProgressEvent) => void>;

    /**
     * Creates loader for .aero files
     *
     * @param loadingOptions options for loading and parsing Aero files.
     */
    constructor(loadingOptions?: AeroLoadingOptions) {
        this._loadingOptions = loadingOptions || AeroFileLoader.DefaultLoadingOptions;
    }

    private static get DefaultLoadingOptions(): AeroLoadingOptions {
        return {
            autoPlay: AeroFileLoader.AUTO_PLAY
        };
    }

    /**
     * Instantiates a Aero file loader plugin.
     * @returns the created plugin
     */
    createPlugin(): ISceneLoaderPluginAsync | ISceneLoaderPlugin {
        return new AeroFileLoader(AeroFileLoader.DefaultLoadingOptions);
    }

    /**
     * If the data string can be loaded directly.
     *
     * @param data string containing the file data
     * @returns if the data can be loaded directly
     */
    public canDirectLoad(data: string): boolean {
        return false;
    }

    /**
     * Imports one or more meshes from the loaded Aero data and adds them to the scene
     * @param meshesNames a string or array of strings of the mesh names that should be loaded from the file
     * @param scene the scene the meshes should be added to
     * @param data the Aero data to load
     * @param rootUrl root url to load from
     * @param onProgress event that fires when loading progress has occured
     * @param fileName Defines the name of the file to load
     * @returns a promise containg the loaded meshes, particles, skeletons and animations
     */

    public importMeshAsync(meshesNames: any, scene: Scene, data: any, rootUrl: string, onProgress?: (event: ISceneLoaderProgressEvent) => void, fileName?: string): Promise<ISceneLoaderAsyncResult> {

        if (onProgress) {
            this._progressCallback = onProgress;
        }

        //get the meshes from Aero file
        return this._parseAeroFile(meshesNames, scene, data, rootUrl).then((meshes) => {
            return {
                meshes: meshes,
                particleSystems: [],
                skeletons: [],
                animationGroups: [],
                transformNodes: [],
                geometries: [],
                lights: []
            };
        });
    }

    /**
     * Imports all objects from the loaded Aero data and adds them to the scene
     * @param scene the scene the objects should be added to
     * @param data the Aero data to load
     * @param rootUrl root url to load from
     * @param onProgress event that fires when loading progress has occured
     * @param fileName Defines the name of the file to load
     * @returns a promise which completes when objects have been loaded to the scene
     */

    public loadAsync(scene: Scene, data: string, rootUrl: string, onProgress?: (event: ISceneLoaderProgressEvent) => void, fileName?: string): Promise<void> {
        //Get the 3D model
        return this.importMeshAsync(null, scene, data, rootUrl, onProgress).then(() => {
            // return void
        });
    }

    /**
     * Load into an asset container.
     * @param scene The scene to load into
     * @param data The data to import
     * @param rootUrl The root url for scene and resources
     * @param onProgress The callback when the load progresses
     * @param fileName Defines the name of the file to load
     * @returns The loaded asset container
     */
    public loadAssetContainerAsync(scene: Scene, data: string, rootUrl: string, onProgress?: (event: ISceneLoaderProgressEvent) => void, fileName?: string): Promise<AssetContainer> {
        var container = new AssetContainer(scene);
        this._assetContainer = container;

        return this.importMeshAsync(null, scene, data, rootUrl).then((result) => {
            result.meshes.forEach((mesh) => container.meshes.push(mesh));
            result.meshes.forEach((mesh) => {
                var material = mesh.material;
                if (material) {
                    // Materials
                    if (container.materials.indexOf(material) == -1) {
                        container.materials.push(material);

                        // Textures
                        var textures = material.getActiveTextures();
                        textures.forEach((t) => {
                            if (container.textures.indexOf(t) == -1) {
                                container.textures.push(t);
                            }
                        });
                    }
                }
            });
            this._assetContainer = null;
            return container;
        }).catch((ex) => {
            this._assetContainer = null;
            throw ex;
        });
    }

    /**
     * Read the Aero file and create an Array of meshes.
     * Each mesh contains all information given by the Aero and the MTL file.
     * i.e. vertices positions and indices, optional normals values, optional UV values, optional material
     * @param meshesNames defines a string or array of strings of the mesh names that should be loaded from the file
     * @param scene defines the scene where are displayed the data
     * @param data defines the content of the Aero file
     * @param rootUrl defines the path to the folder
     * @returns the list of loaded meshes
     */

    private _parseAeroFile(meshesNames: any, scene: Scene, data: string, rootUrl: string): Promise<Array<AbstractMesh>> {
        this.scene = new AeroScene(scene);
        var url: string = rootUrl;
        if (url[url.length - 1] == '/') {
            url = url.substring(0, url.length - 1);
        }
        var componentMap = null;
        if (!url.includes("adobeaero.app.link")) {

            if (url.includes("file://DCX")) {
                componentMap = dcxInfo;
                url = componentMap["SCENE_JSON"];
            } else {
                url += "scene.aero";
            }
        }
        return this.LoadAeroWebScene(url, "", componentMap, this._progressCallback).then((meshes: Array<AbstractMesh>) => {
            return meshes;
        });
    }

}

if (SceneLoader) {
    //Add this loader into the register plugin
    SceneLoader.RegisterPlugin(new AeroFileLoader());
}