declare function importScripts(...urls: string[]): void;
declare var _native: any;

export class AeroTask {
    public name: string;
    public loadedMeshes: AbstractMesh[];
    constructor () {
        this.name = "";
    }
}

export class AeroQueueEntry {
    public isLoading: boolean = false;
    public isLoaded: boolean = false;
    public url: string;
    public node: any;
    public maxDiameter: any;
    public isAdded: boolean = false;
    public useDefaultLoadingScreen: boolean = false;
    constructor () {
        this.isLoading = false;
        this.isLoaded = false;
        this.url = "";

    }
}

export class AeroScene {

    public isDesktop: boolean = false;
    constructor(scene: Scene) {
        this.queueEmpty = true;
        this.currentlyLoadingAssetID = "";
        this.babylonScene = scene;
        this.babylonScene.useRightHandedSystem = true;
        this.behaviorEngine = new AeroBehaviorEngine(scene);
        this.webXR = new AeroWebXRHelper(scene, this);

        var isDesktop = {
            Mac: function(): boolean {
                return navigator.platform != undefined && navigator.platform.includes("MacIntel");
            },
            Windows: function(): boolean {
                return navigator.platform != undefined && navigator.platform.includes("Win32");
            },
            any: function(): boolean {
                return (isDesktop.Mac() || isDesktop.Windows());
            }
        };
        this.isDesktop = isDesktop.any();

        this.clear();
    }

    //Initialize
    public InitializeSceneLoader() {
        SceneLoader.OnPluginActivatedObservable.add(function (plugin: ISceneLoaderPlugin | ISceneLoaderPluginAsync, eventState: EventState) {
            if (plugin.name === "gltf" && plugin instanceof GLTFFileLoader) {
                //Don't play animations on load
                plugin.animationStartMode = GLTFLoaderAnimationStartMode.NONE;
            }
        });

    }

    //Play the experience
    public Start() {
        this.behaviorEngine.OnStart();
    }

    //WebXR
    private webXR: AeroWebXRHelper ;
    public GetWebXR() {
        return this.webXR;
    }

    public EnterARModeiOS() {
        this.webXR.EnterARMode(false);
    }
    public ExitARModeiOS() {
        this.webXR.ExitARMode(false);
    }
    //Basics
    private babylonScene: Scene;
    private entities: Map<string, AeroEntity>;
    private entityMapping: Map<AbstractMesh, string> = new Map<AbstractMesh, string>(); //meshName, entityName
    public GetEntityMapping() {
        return this.entityMapping;
    }

    //URL Stuff
    private sceneURL: string;
    private baseURL: string;
    private sceneFolderURL: string;
    private componentMap: Map<string, string>;
    private loadingChildScene: boolean = false;

    //Asset URLs
    private viewerAssetPath: string = "";
    private iblURL: string = "";
    private earcutURL: string = "";
    private publishURL: string = "";
    private iOSWebXRURL: string = "";
    public GetIBLURL() {
        return this.iblURL;
    }
    public GetAssetURL(assetName: string): string {
        return this.viewerAssetPath + assetName;
    }
    public GetEarcutURL(): string {
        return this.earcutURL;
    }
    public GetIOSWebXRURL(): string {
        return this.iOSWebXRURL;
    }
    public GetSceneURL(): string {
        return this.sceneURL;
    }
    public GetPublishURL(): string {
        return this.publishURL;
    }
    public SetPublishURL(url: string) {
        this.publishURL = url;
    }
    //Queue
    private queueEmpty: boolean;
    private assetsManager: Map<string, AeroQueueEntry> = new Map<string, AeroQueueEntry>();
    private assetsLoaded: number = 0;
public progressCallback: any; //TODO: make accessor
    private sceneLoadedCallback: any; //TODO: make accessor
    private currentlyLoadingAssetID: string;

    //Scene size
    public sceneWidth: number = 0;
    public sceneDepth: number = 0;
    public sceneHeight: number = 0;
    private sceneCenter: Vector3 = new Vector3(0, 0, 0);
    private sceneBottom: Vector3 = new Vector3(0, 0, 0);

    //Shadows
    private shadowLight: DirectionalLight;
    private aeroShadowGenerator: ShadowGenerator;
    public SetShadowDirection(x: number, y: number, z: number) {
        if (this.shadowLight) {
            this.shadowLight.direction = new Vector3(x, y, z);
        }
    }

    /*

    Behaviors - are stored as any and fed to the behavior engine "blind"
    Mapping between babylon scene meshes and entity names - Needed for mesh picking

    */

    private behaviorEngine: AeroBehaviorEngine;
    public GetRTM() {
        return this.behaviorEngine.GetRTM();
    }
    public RaiseCustomEvent(eventName: string) {
        return this.behaviorEngine.RaiseCustomEvent(eventName);
    }
    public pointerDownPredicate = function (mesh: any) {
        return (mesh.isPickable &&
            mesh.isVisible &&
            mesh.isReady() &&
            mesh.isEnabled()); // &&
            //(!scene.cameraToUseForPointers || (scene.cameraToUseForPointers.layerMask & mesh.layerMask) !== 0));
    };
    private setupPicking() {
        this.babylonScene.onPointerObservable.add((pointerInfo: PointerInfo) => {
            //pointerInfo.event.preventDefault();
            switch (pointerInfo.type) {
                case PointerEventTypes.POINTERDOWN:
                    var pickInfo = this.babylonScene.pick(pointerInfo.event.x, pointerInfo.event.y, this.pointerDownPredicate, false, this.babylonScene.activeCamera);
                    if (pointerInfo && pickInfo) {
                        pointerInfo.pickInfo = pickInfo;
                        if (pointerInfo.pickInfo.pickedMesh != undefined) {
                            var mesh: AbstractMesh = pointerInfo.pickInfo.pickedMesh;

                            if (this.childScene && this.childScene.entityMapping.has(mesh)) {
                                var entityName: string = this.childScene.getEntityForMesh(mesh) as string;
                                this.childScene.behaviorEngine.OnTap(entityName);
                            } else if (this.entityMapping.has(mesh)) {
                                var entityName: string = this.getEntityForMesh(mesh) as string;
                                this.behaviorEngine.OnTap(entityName);
                            }
                        }
                    }
                    break;
                case PointerEventTypes.POINTERUP:
                    break;
                case PointerEventTypes.POINTERMOVE:
                    break;
                case PointerEventTypes.POINTERWHEEL:
                    break;
                case PointerEventTypes.POINTERPICK:
                    if (pointerInfo && pointerInfo.pickInfo) {
                        if (pointerInfo.pickInfo.pickedMesh != undefined) {
                            var mesh: AbstractMesh = pointerInfo.pickInfo.pickedMesh;

                            if (this.childScene && this.childScene.entityMapping.has(mesh)) {
                                var entityName: string = this.childScene.getEntityForMesh(mesh) as string;
                                this.childScene.behaviorEngine.OnTap(entityName);
                            } else if (this.entityMapping.has(mesh)) {
                                var entityName: string = this.getEntityForMesh(mesh) as string;
                                this.behaviorEngine.OnTap(entityName);
                            }
                        }
                    }
                    break;
                case PointerEventTypes.POINTERTAP:
                    this.webXR.OnTap();
                    break;
                case PointerEventTypes.POINTERDOUBLETAP:
                    break;
            }
        });
    }
    private addEntityMapping(mesh: AbstractMesh, name: string) {
        return this.entityMapping.set(mesh, name);
    }
    private getEntityForMesh(mesh: AbstractMesh) {
        return this.entityMapping.get(mesh);
    }

    /*

    Anchors

    */

    private anchorNode: AeroAnchorNode;
    public GetImageMarkerURL() {
        var uri = this.anchorNode.imageMarkerPath;
        var assetURL = this.sceneFolderURL + uri;
        if (this.componentMap != undefined && this.componentMap.get(uri) != undefined) {
            assetURL = this.componentMap.get(uri) as string;
        }
        return assetURL;
    }

    public SceneHasImageMarkers() {
        if (this.anchorNode.imageMarkerPath != "" && this.anchorNode.imageMarkerPath != undefined) {
            return true;
        }
        return false;
    }

    public GetAnchor() {
        return this.anchorNode;
    }

    public ResetCameraAndAnchor() {

        this.anchorNode.position.x = 0;
        this.anchorNode.position.y = 0;
        this.anchorNode.position.z = 0;

        this.anchorNode.rotation.x = 0;
        this.anchorNode.rotation.y = 0;
        this.anchorNode.rotation.z = 0;

        //FIXME AddGroundPlane - var center = this.anchor.position;
        // if(this.trackingMode == "plane" && this.groundOrientation == 1) {
            this.webXR.GetEnvironment().AddGroundPlane(this.sceneBottom, this.sceneWidth);
            if (this.aeroShadowGenerator) {
                 this.aeroShadowGenerator.recreateShadowMap();
            }
        //}

    }

    /*

    File Loading

    */

    public LoadScene(url: string, componentMap: Map<string, string>, options: any): boolean {
        if (url == "") {
            return false;
        }
        if (options == undefined) {
            options = new Object();
        }
        this.viewerAssetPath = options.viewerAssetsURL;
        this.iblURL = options.iblURL;
        this.earcutURL = options.earcutURL;
        this.iOSWebXRURL = options.iOSWebXRURL;
        var prefix: string = "./";
        if (typeof _native !== 'undefined') {
            prefix = "app:///out/";
        }
        if (this.iOSWebXRURL == undefined) {
            this.iOSWebXRURL =  prefix + "js/aeroWebXRIOS.js";
        }
        if (this.viewerAssetPath == undefined) {
            this.viewerAssetPath = prefix + "ui/viewer/";
        }
        if (this.iblURL == undefined) {
            this.iblURL =  prefix + "ibl/aero/ibl_aero.env";
        }
        if (this.earcutURL == undefined) {
            this.earcutURL =  prefix + "js/earcut.min.js";
        }
        // var iOSWebXRURL: string = this.GetIOSWebXRURL();
        // if(iOSWebXRURL != "") {
        //     if (IsWindowObjectExist()) {
        //         Tools.LoadScriptAsync(iOSWebXRURL);
        //     } else {
        //         importScripts(iOSWebXRURL);
        //     }
        // }
        this.sceneURL = url;
        this.baseURL = url.substring(0, url.lastIndexOf("/") + 1);
        this.sceneFolderURL = this.sceneURL.substring(0, this.sceneURL.lastIndexOf("/") + 1);
        this.componentMap = componentMap;
        this.behaviorEngine.SetComponentMap(componentMap);
        this.loadFileFromURL(url, "application/json", (jsonText: string) => {
            this.parseAeroScene(jsonText);
        });
        return true;
    }

    private loadFileFromURL(file: string, mimeType: string, callback: Function)
    {
        if (file.includes("undefined")) { return; }
        var request = new WebRequest();
        request.addEventListener("readystatechange", () => {
            if (request.readyState === 4 && (request.status === 200 || request.status === 0)) {
                if (mimeType != undefined && mimeType.includes("image") || mimeType.includes("octet")) {
                    var byteArray = new Uint8Array(request.response);
                    callback(byteArray);
                } else {
                    callback(request.responseText);
                }
            }
        });
        request.open('GET', file);
        if (mimeType != undefined && mimeType.includes("image") || mimeType.includes("octet")) {
            request.responseType = "arraybuffer";
        }
        //request.overrideMimeType(mimeType);
        request.send();
    }

    private enableShadows: boolean = false;
    private parseAeroScene(jsonText: any) {
        this.parseJSON(jsonText, () => {

            var scene = this.babylonScene;
            if (!scene) {
                return;
            }

            if (!this.loadingChildScene) {
                if (this.enableShadows) {
                    //Lights and Shadows
                    this.shadowLight  = new DirectionalLight("DirectionalLight", new Vector3(0, -10, 0), scene);
                    this.shadowLight.intensity = 0.0;
                    this.aeroShadowGenerator = new ShadowGenerator(1024, this.shadowLight);
                    this.aeroShadowGenerator.useBlurExponentialShadowMap = true;
                }

                var iblTexture: CubeTexture = CubeTexture.CreateFromPrefilteredData(this.iblURL, scene, ".env");
                scene.environmentTexture = iblTexture;
                scene.environmentIntensity = 1.0;
                scene.clearColor = new Color4(0, 0, 0, 0);

                //Install an event handler
                this.setupPicking();
            }

            //Walk the entities we parsed, add them to the download queue
            this.visitRootEntities((entity: AeroEntity, object: any) => {
                this.addEntityToBabylonScene(entity, (babylonEntityNode: TransformNode, assetURL: string, entityName: string, maxDiameter: number) => {
                    //Gets called back for each entity found in the file that has an asset to load
                    this.addAssetToQueue(babylonEntityNode, assetURL, entityName, maxDiameter);
                });
            }, this);
        });
    }

    private parseJSON(jsonText: any, sceneParsedCallback: Function) {
        var json = JSON.parse(jsonText);
        //this.clear();
        const entityFromJson = (json: any, parent: any) => {
            let name = json.name;
            if (name) {
                var uri = json.filename;
                if (json["animationClip.filename"] != undefined) {
                    uri = json["animationClip.filename"];
                } else if (json["userBehavior.filename"] != undefined) {
                    uri = json["userBehavior.filename"];
                }

                let entity = new AeroEntity();
                if (name == "ag_anchor") {
                    this.createAnchor(json);
                } else if (uri) {
                    var extension: string = uri.substring(uri.lastIndexOf(".") + 1);
                    if (extension == "js") {
                        entity = this.createEntity(name, parent);
                        this.behaviorEngine.RunBehaviorJSON(uri);
                    } else {
                        entity = this.createAsset(name, uri, parent);
                        if (json.maxAssetDiameter) {
                            entity.asset().setMaxDiameter(json.maxAssetDiameter);
                        }
                        if (json.bboxMin && json.bboxMax) {
                            var min: Vector3 = new Vector3();
                            min.x = json.bboxMin[0];
                            min.y = json.bboxMin[1];
                            min.z = json.bboxMin[2];
                            var max: Vector3 = new Vector3();
                            max.x = json.bboxMax[0];
                            max.y = json.bboxMax[1];
                            max.z = json.bboxMax[2];

                            entity.asset().setBounds({
                                min: min,
                                max: max
                            });
                        }
                    }
                } else {
                    entity = this.createEntity(name, parent);
                }
                if (json.AeroQuaternion) {
                    entity.setQuaternion(json.AeroQuaternion);
                }
                if (json.rotation) {
                    entity.setRotation(json.rotation);
                }
                if (json.scale) {
                    entity.setScale(json.scale);
                }
                if (json.translation) {
                    entity.setTranslation(json.translation);
                }
                if (json.canDraw != undefined) {
                    entity.setCanDraw(json.canDraw);
                }
                const children = json.children;
                for (const childI in children) {
                    entityFromJson(children[childI], entity);
                }
                return entity;
            }
            return new AeroEntity();
        };

        if (json) {
            const entities = json.nodes;
            if (entities) {
                for (const entityI in entities) {
                    entityFromJson(entities[entityI], null);
                }
            }
            this.behaviorEngine.InitializeBehaviors(json, this.sceneFolderURL, sceneParsedCallback);
        }
    }

    private visitRootEntities(func: Function, object: any) {
        this.entities.forEach((entity: AeroEntity, key: string) => {
            if (!entity.hasParent()) {
                func(entity, object);
            }
        });
    }

    private addEntityToBabylonScene(entity: AeroEntity, addNodeCallback: any) {

        //An entity becomes a Transform node with children for the meshes that make up the entity
        //This generally is the node that the behaviors act on
        var node = new TransformNode(entity.localName(), this.babylonScene);

        //Set parent
        if (entity.parent()) {
            node.parent = entity.parent().babylon;
        } else {
            node.parent = this.anchorNode;
        }

        //Position
        var translationAero: Vector3 = entity.translation();
        var translationArray: Array<number> = translationAero.asArray();
        node.position = Vector3.FromArray(translationArray);

        //Rotation
        if (entity.useQuaternion()) {
            var quatAero: Quaternion = entity.quaternion();
            node.rotationQuaternion = Quaternion.FromArray(quatAero.asArray());
        } else {
            let rotation: Vector3 = entity.rotation();
            const deg2rad = Math.PI / 180.0;
            // Right to Left handed
            node.rotate(Axis.X, rotation.x * deg2rad, Space.LOCAL);
            node.rotate(Axis.Y, rotation.y * deg2rad, Space.LOCAL);
            node.rotate(Axis.Z, rotation.z * deg2rad, Space.LOCAL);
        }

        //Scale
        let aeroScale: Vector3 = entity.scale();
        node.scaling = Vector3.FromArray(aeroScale.asArray());
        entity.babylon = node;

        //URL
        //If an asset, let host know
        const asset = entity.asset();
        const uri = asset.uri();
        if (uri) {
            node.name = entity.globalName();
            if (node.name.includes("/node")) {
                node.name = node.name.substring(0, node.name.lastIndexOf("/node"));
            }
            if (!entity.canDraw()) {
                node.setEnabled(false);
            }
            //Host needs to add this one to the scene
            addNodeCallback(node, uri, entity.globalName(), entity.asset().maxDiameter());
        }

        //Recurse for children
        entity.visitChildren((child: AeroEntity, object: any) => {
            this.addEntityToBabylonScene(child, addNodeCallback);
        }, this);
    }

    private createAsset(name: any , uri: any , parent: any) {
        const entity = this.createEntity(name, parent);
        entity.asset().setUri(uri);
        return entity;
    }

    private createEntity(name: any , parent: any) {
        const entity = new AeroEntity();
        if (parent) {
            entity.setParent(parent);
        }
        entity.setLocalName(name);
        this.entities.set(entity.globalName(), entity);
        return entity;
    }

    private createAnchor(anchor: any) {
        var anchorName: string = "Ag_Anchor";
        this.loadingChildScene = false;
        var sceneAnchor: Nullable<AeroAnchorNode> = this.babylonScene.getNodeByName(anchorName) as Nullable<AeroAnchorNode>;
        if (sceneAnchor)  {
            this.loadingChildScene = true;
            anchorName = "Ag_Anchor_2";
        }

        this.anchorNode = new AeroAnchorNode(anchorName, this.babylonScene, this);
        this.anchorNode.imageMarkerPath = anchor["imageMarker.filename"];
        this.anchorNode.physicalWidth = anchor["imageMarker.physicalWidth"];
        this.anchorNode.desiredAlignment = anchor["anchor.desiredAlignment"];
        // if(sceneAnchor) {
        //     this.anchorNode.setParent(sceneAnchor);
        // }
    }

    public clear() {
        this.entities = new Map<string, AeroEntity>();
    }

    /*

    Asset Loading Queue

    */

    private addAssetToQueue(entityNode: TransformNode, uri: string, entityName: string, maxDiameter: number) {
        if (uri) {
            var assetURL = this.baseURL + uri;
            if (this.componentMap != undefined && this.componentMap.get(uri) != undefined) {
                assetURL = this.componentMap.get(uri) as string;
            }

            var queueEntry = new AeroQueueEntry();
            queueEntry.isLoaded = false;
            queueEntry.isLoading = false;
            queueEntry.isAdded = false;
            queueEntry.useDefaultLoadingScreen = false;
            queueEntry.node = entityNode;
            queueEntry.url = assetURL;
            queueEntry.maxDiameter = maxDiameter;

            this.assetsManager.set(entityName, queueEntry);

            //Start an interval to check pump the queue
            if (this.queueEmpty == true) {
                this.queueEmpty = false;
                setTimeout(() => {
                    this.checkAssetQueue();
                }, 1000);
            }
        }
    }

    private checkAssetQueue()
    {

        if (this.queueEmpty == true) {
            //Kill the timeout
            //clearInterval(this.queueTimeout);
            return;
        }

        //Progress for Assets
        var numLoading = 0;
        var maxLoading = 1;
        var broken = false;
        this.assetsManager.forEach((asset: AeroQueueEntry, assetID: string) => {
                //let asset = this.assetsManager.get(assetID);
            if (asset && asset.isLoading && !broken) {
                numLoading++;
                if (numLoading >= maxLoading) {
                    broken = true;
                    //return;
                }
            }
        });
        this.assetsLoaded = 0;

        this.assetsManager.forEach((asset: AeroQueueEntry, assetID: string) => {
            if (!asset.isLoaded && !asset.isLoading && !broken) {
                //var percent = 100 * (this.assetsLoaded / this.assetsManager.size); // + this.gifs.length));
                if (this.progressCallback) {
                    this.progressCallback(true, this.assetsLoaded, this.assetsManager.size);
                }
                asset.isLoading = true;
                this.currentlyLoadingAssetID = assetID;
                SceneLoader.ImportMesh("", asset.url, "", this.babylonScene,  (meshes: AbstractMesh[]
                    ) => {
                    var task = new AeroTask();
                    task.name = this.currentlyLoadingAssetID;
                    task.loadedMeshes = meshes;
                    this.assetLoaded(task);
                }, () => {
                    //Progress
                },  () => {
                    //Error
                    var task = new AeroTask();
                    task.name = this.currentlyLoadingAssetID;
                    task.loadedMeshes = [];
                    this.assetLoaded(task);
                }, ".glb");

                broken = true;
            }
            if (!broken) {
                this.assetsLoaded = this.assetsLoaded + 1;
            }
        });

        //If we made it this far, the scene is loaded
        if (!broken) {
            this.onSceneLoaded();
        } else {
            setTimeout(() => {
                this.checkAssetQueue();
            }, 1000);
        }
    }

    private assetLoaded(task: AeroTask) {

        let foundRoot = false;
        let asset: AeroQueueEntry = this.assetsManager.get(task.name) as AeroQueueEntry;
        if (!asset) {
            return;
        }

        const maxAssetDiameter = asset.maxDiameter;

        var node = asset.node;
        var adjustment = new TransformNode('adjustment', this.babylonScene);
        adjustment.parent = node;

        // var addProximity = false;
        var entityName = task.name.substring(0, task.name.lastIndexOf("/"));

        // let proximityBehavior = this.behaviorEngine.proximityBehaviors.get(entityName);
        // if (proximityBehavior) {
        //     addProximity = true;
        // }

        for (const meshI in task.loadedMeshes) {
            const mesh = task.loadedMeshes[meshI];

            //FIXME: (Perf - every mesh?) Only do this if this mesh has an "OnProximity" event
            // if (addProximity) {
            //     this.SetupCollisionDetection(mesh);
            // }

            mesh.cullingStrategy = AbstractMesh.CULLINGSTRATEGY_BOUNDINGSPHERE_ONLY;

            //FIXME: Shadows - can I do this on the outside using a Transform/Group?
            if (this.aeroShadowGenerator) {
                this.aeroShadowGenerator.addShadowCaster(mesh);
            }

            if (mesh.parent === null) {
                if (!foundRoot) {

                    foundRoot = true;

                    const bounds = mesh.getHierarchyBoundingVectors();

                    // Parent after the bounds are computed since they happen in world space
                    mesh.parent = adjustment;

                    const radius = Math.max(bounds.max.x - bounds.min.x,
                                            bounds.max.y - bounds.min.y,
                                            bounds.max.z - bounds.min.z);
                    if (maxAssetDiameter > 0.000001) {
                        let scale = maxAssetDiameter / radius;
                        adjustment.scaling.x = scale;
                        adjustment.scaling.y = scale;
                        adjustment.scaling.z = scale;
                        adjustment.position.x = -.5 * (bounds.max.x + bounds.min.x) * scale;
                        adjustment.position.y = -.5 * (bounds.max.y + bounds.min.y) * scale;
                        adjustment.position.z = -.5 * (bounds.max.z + bounds.min.z) * scale;
                    }
                }
            }
            this.addEntityMapping(mesh, entityName);
        }

        asset.isLoaded = true;
        asset.isLoading = false;

    }

    private onSceneLoaded()
    {
        //console.log("Aero Scene Loaded");
        this.queueEmpty = true;

        //Get the scene bounds
        var sceneBounds = this.anchorNode.getHierarchyBoundingVectors();
        this.sceneWidth = sceneBounds.max.x - sceneBounds.min.x;
        this.sceneDepth = sceneBounds.max.z - sceneBounds.min.z;
        this.sceneHeight = sceneBounds.max.y - sceneBounds.min.y;
        this.sceneCenter = new Vector3(0, 0, 0);
        this.sceneCenter.x = sceneBounds.min.x + this.sceneWidth / 2;
        this.sceneCenter.y = sceneBounds.min.y + this.sceneHeight / 2;
        this.sceneCenter.z = sceneBounds.min.z + this.sceneDepth / 2;

        this.sceneBottom = new Vector3(0, 0, 0);
        this.sceneBottom.x = sceneBounds.min.x + this.sceneWidth / 2;
        this.sceneBottom.y = sceneBounds.min.y;
        this.sceneBottom.z = sceneBounds.min.z + this.sceneDepth / 2;

        //Set up 3D Animations
        this.behaviorEngine.PrepareBehaviors(this.entityMapping);

        //Initialie WebXR
        this.ResetCameraAndAnchor();

        this.webXR.InitializeWebXR(() => {
            //WebXR is initialized.  Start Behaviors
            this.Start();

            if (typeof _native !== 'undefined') {
                //For Babylon Native - for now
                this.webXR.xrHelper.baseExperience.enterXRAsync("immersive-ar", "unbounded", this.webXR.xrHelper.renderTarget).then((xrSessionManager) => {
                    // Pass through, head mounted displays (HoloLens 2) require autoClear and a black clear color
                    xrSessionManager.scene.autoClear = true;
                    xrSessionManager.scene.clearColor = new Color4(0, 0, 0, 0);
                });
            }
        });
        //Call back if needed
        if (this.sceneLoadedCallback) {
            this.sceneLoadedCallback(this.GetLoadedMeshes());
        }

    }
    public SetSceneLoadedCallback(callback: Function) {
        this.sceneLoadedCallback = callback;
    }

    public SetEnterARCallback(callback: Function) {
        this.webXR.SetEnterARCallback(callback);
    }

    public SetExitARCallback(callback: Function) {
        this.webXR.SetExitARCallback(callback);
    }

    public GetLoadedMeshes() {
        var meshes: Array<AbstractMesh> = new Array<AbstractMesh>();
        this.entityMapping.forEach((entity: string, mesh: AbstractMesh) => {
            meshes.push(mesh);
        });
        return meshes;
    }

    private childScene: Nullable<AeroScene> = null;
    public PlaceScene(anchorNode: AeroAnchorNode) {
        this.childScene = anchorNode.aeroScene;
        if (this.webXR) {
            this.webXR.PlaceScene(anchorNode);
        }
    }

}