/*

Triggers and Actions

Triggers:
    Start
    Touch
    Proximity: https://doc.babylonjs.com/divingDeeper/animation/render_frame_animation

Actions:
    Action Base Class - subject, duration, easing, play count, delay, infinite, back and forth
        Use this (?) - https://doc.babylonjs.com/typedoc/classes/animation

    Actions (derive from base class):
        Wait (Action) - Default behavior

    Simple
        Show (ShowAction)
        Hide (HideAction)
        Open URL (OpenURLAction) - url (external or new experience in place)

    Media
        Play animation (Play3DAnimationAction) - set, clip, length, speed, transition, treadmill
        Play images (PlayImagesAction) - trim, seconds per frame, speed
        Play audio (PlayAudioAction) - clip, ambient, volume

    Transform (coordinate frame)
        Move (MoveAction) - space, offset (x,y,z)
        Scale (ScaleAction) - space, multiplier
        Rotate (RotateAction) - space, rotation (p, y, r)
        Spin (SpinAction) - space, axis, reverse direction
        Bounce (BounceAction) - space, offset (x,y,z), rebounds

    Transform (absolute)
        Orbit (OrbitAction) - center
        Move to (MoveToAction) - location, distance between, speed
        Aim (AimAction) - subject, target, billboard only: https://doc.babylonjs.com/divingDeeper/animation/render_frame_animation

Actions Approaches:
Write in typescript, connvet to C++ - https://github.com/ASDAlexander77/TypeScript2Cxx
Write in C++, convert to JS with Emscriptm- https://www.sitepoint.com/getting-started-emscripten-transpiling-c-c-javascript-html5/

*/

