    //Private below here
    private babylonScene: Scene;
    private rawBehaviors: Array<any> = new Array<any>();
    private visitBehaviors(func: Function, object: any) {
        for (const behaviorI in this.rawBehaviors) {
            const behavior = this.rawBehaviors[behaviorI];
            func(behavior, object);
        }
    }

    private tapBehaviors: Map<string, Array<AeroBehavior>> = new Map<string, Array<AeroBehavior>>();
    private proximityBehaviors: Map<string, Array<AeroBehavior>> = new Map<string, Array<AeroBehavior>>();
    private startBehaviors: Array<AeroBehavior> = new Array<AeroBehavior>();

    //Needed to resolve URLs to any assets
    private componentMap: Map<string, string>;
    private sceneFolderURL: string;

    //Ready-to-play Animations
    private resolvedAnimationTargets: Map<string, Map<string, AnimationGroup>> =  new Map<string, Map<string, AnimationGroup>>();

    /*

    Behavior Prep - Gets called after the scene has loaded completely but before Start

    */

    private prepareBehaviors() {
        this.initializeAnimations();
        this.initializeSounds();

        /*
        Wait for engine to be ready
        Gather and send
            PlayAnimation Data
                Mesh hiearchies - from scene.loadedMeshes
                Keyframes from Animation Groups - from this.resolvedAnimationTargets
                Bindings between meshes and groups - from this.resolvedAnimationTargets
            PlayImages Data
                Frame count
                Frame rate
        */
    }

    /*

    Behavior Execution

    */

    //Start playing the behaviors
    private start() {
        if (!this.useWASM) {
            for (const behaviorI in this.startBehaviors) {
                this.executeBehavior(this.startBehaviors[behaviorI]);
            }
        } else {
            AeroBehaviorEngine.wasmBehaviorEngine.Play();
        }
    }

    private gatherParallelActions(action: any, actionList: any) {
        actionList.push(action);
        if (action.parallel != undefined) {
            actionList = this.gatherParallelActions(action.parallel, actionList);
        }
        return actionList;
    }

    private getActionSubject(action: any): TransformNode {
        var subjectName = action.subject ;

        var actionSubject: TransformNode = this.babylonScene.getNodeByName(subjectName) as TransformNode;
        return actionSubject;
    }

    private executeBehavior(behavior: any) {

        var actionList: Array<any> = new Array();
        actionList = this.gatherParallelActions(behavior.action, actionList);

        for (let a in actionList) {
            this.handleAction(actionList[a]);
        }

    }

